interface RxListBuilderItemDefault {
  /**
   * Required property
   */
  id: string | number;
  name?: string;
  disabled?: boolean;
  selected?: boolean;
  children?: RxListBuilderItem[];
  hideEdit?: boolean;
  hideDelete?: boolean;
}

export type RxListBuilderItem<T = RxListBuilderItemDefault> = Partial<T>;

export interface ListBuilderTextsModel {
  requiredText?: string;
  searchPlaceholder?: string;
  headerText?: string;
  numberOptionsText?: string;
  notFound?: string;
  add?: string;
  edit?: string;
  save?: string;
  cancel?: string;
  delete?: string;
  deleteTooltip?: string;
  selectAll?: string;
  clear?: string;
}

export type RxListBuilderSelectionMode = 'single' | 'multiple';
